import { h } from 'preact';
import { Link } from 'preact-router/match';
import style from './style';
import { STORAGE_VERSION } from '../../config/useConfig';

import Toggle from './toggle';

const Header = ({
	currentUrl,
	config,
	ir,
}) => (
	<header class={style.header}>
		<div class={style.title}>
			<h1>
				<a href="./table">
					<span class={style.deco}>:.:</span>  明日方舟 | PRTS.Map
					<span class={style.version}>V{STORAGE_VERSION}</span>
				</a>
			</h1>
		</div>
		<div class={style.nav_group}>
			<nav>
				<Link activeClassName={style.active} href="/">{ir('header-nav-homepage', '首页')}</Link>
				<Link activeClassName={style.active} href="/map">{ir('header-nav-map', '地图')}</Link>
				<Link activeClassName={style.active} href="/backup">{ir('header-nav-backup', '数据导出')}</Link>
			</nav>
		</div>
		{
			currentUrl === '/map' && (
				<div class={style.toggles}>
					<Toggle
						value
						toggle={value => value}
						content="选项"
					/>
				</div>
			)
		}
	</header>
);

export default Header;
