import React from 'preact';
import { Router } from 'preact-router';
import { useState, useEffect } from 'preact/hooks';

import useConfig from './config/useConfig';

import Header from './components/header';

import ArkInfo from './routes/info';
import ArkMapEditor from './routes/map';
import ArkBackup from './routes/backup';

import useData from './models/useData';
import localeRender from './i18n/render';

const App = (props) => {
	const [currentUrl, setCurrentUrl] = useState('/');
	const {
		config,
		load,
	} = useConfig();

	const data = useData();
	const [isMac, setIsMac] = useState(false);
	const [isEdge, setIsEdge] = useState(false);
	const [isMobile, setIsMobile] = useState(false);

	useEffect(() => {
		load();

		const ua = window.navigator.userAgent && window.navigator.userAgent.toLowerCase() || '';

		setIsMac(ua.indexOf('mac') >= 0);
		setIsEdge(ua.indexOf('edge') >= 0);
		setIsMobile(
			ua.indexOf('iphone') >= 0
			|| ua.indexOf('android') >= 0
		);
	}, []);

	const env = {
		isMac,
		isEdge,
		isMobile,
		default_disable_perspective: isEdge || isMobile,
	};

	const ir = localeRender(config.locale);

	return (
		<div id="app">
			<Header
				currentUrl={currentUrl}
				config={config}
				ir={ir}
			/>
			<Router onChange={e => {
				global.ga('set', 'page', e.url);
				global.ga('send', 'pageview');
				setCurrentUrl(e.url);
			}}
			>
				<ArkInfo path="/" />
				<ArkMapEditor path="/map"
					config={config}
					data={data}
					env={env}
					ir={ir}
				/>
				<ArkMapEditor path="/editor"
					config={config}
					data={data}
					env={env}
					ir={ir}
					editorMode
				/>
				<ArkBackup path="/backup" state={data.state} load={data.load} />
			</Router>
		</div>
	);
};

export default App;
